<template>
  <div class="orc">
    <div class="orc-left">
      <div class="orc-left_part1">
        <div class="orc-left_part1-action">
          <template v-if="!inputShow">
            <input
              type="file"
              name="uploadimg"
              id="uploadimg"
              class="inputfile"
              @change="uploadImg($event, 'localFile')"
            />
            <label for="uploadimg" class="upload-image">上传图片</label>
            <button type="button" class="online-url" @click="inputShow = true">
              在线地址
            </button>
          </template>
          <template v-else>
            <Row
              class="part1-input"
              type="flex"
              justify="center"
              align="middle"
            >
              <i-col span="10">
                <Input v-model="imageUrl" placeholder="请输入地址" />
              </i-col>
              <i-col span="5">
                <button
                  type="button"
                  class="online-url"
                  @click="uploadImg($event, 'online')"
                >
                  确定
                </button>
              </i-col>
              <i-col span="5">
                <button
                  type="button"
                  class="online-url"
                  @click="inputShow = false"
                >
                  返回
                </button>
              </i-col>
            </Row>
          </template>
        </div>
        <img class="orc-left_part1-img" :src="imgSrc" />
      </div>
      <div class="orc-left_part2">
        <Row
          class="orc-left_part2-row"
          type="flex"
          justify="start"
          align="middle"
        >
          <i-col span="5">需要识别的语言：</i-col>
          <i-col span="6">
            <Select v-model="model1">
              <Option
                v-for="item in languageList"
                :value="item.value"
                :key="item.value"
                >{{ item.label }}</Option
              >
            </Select>
          </i-col>
        </Row>
        <Row
          class="orc-left_part2-row"
          type="flex"
          justify="start"
          align="middle"
        >
          <i-col span="5">将文本翻译成：</i-col>
          <i-col span="6">
            <Select v-model="model2" disabled>
              <Option
                v-for="item in languageList"
                :value="item.value"
                :key="item.value"
                >{{ item.label }}</Option
              >
            </Select>
          </i-col>
        </Row>
      </div>
      <div class="orc-left_part3">
        <Button class="image-to-text" :loading="loading" @click="youDao"
          >识别图片</Button
        >
        <Button class="translated-text" disabled>翻译文本</Button>
      </div>
    </div>
    <div class="orc-right">
      <div class="orc-right_part1">
        <Input
          v-model="value6"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 12 }"
          placeholder="从图片识别的文字"
        />
      </div>
      <div class="orc-right_part2">
        <Input
          v-model="value7"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 12 }"
          placeholder="翻译的文本"
        />
      </div>
      <div class="orc-right_part3">
        <Input
          v-model="value8"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 12 }"
          placeholder="合成的图片"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { sha256 } from "js-sha256";
import qs from "qs";

export default {
  name: "OCR",
  data() {
    return {
      languageList: [
        {
          value: "chs",
          label: "简体中文"
        },
        {
          value: "eng",
          label: "英文"
        }
      ],
      loading: false,
      value6: "",
      value7: "",
      value8: "",
      model1: "chs",
      model2: "",
      inputShow: false,
      imageUrl: "",
      imgSrc:
        "https://img.alicdn.com/imgextra/i3/897609396/TB1UcuOXd685uJjSZFsXXX8qVXa_!!0-item_pic.jpg_430x430q90.jpg",
      currentType: "url",
      errorMessage: "",
      responseMessage: ""
    };
  },
  methods: {
    uploadImg(e, type) {
      // this.$Message.info(`上传${type}`);
      // console.log(e);
      if (type == "localFile") {
        let file = e.target.files[0];
        let reader = new FileReader();
        reader.onload = () => {
          var base64 = reader.result;
          // console.log(base64);
          this.imgSrc = base64;
        };
        reader.readAsDataURL(file);
        this.currentType = "upload";
      } else {
        //imageUrl
        var xhr = new XMLHttpRequest();
        xhr.onload = () => {
          var reader = new FileReader();
          reader.onloadend = () => {
            var base64 = reader.result;
            this.imgSrc = base64;
          };
          reader.readAsDataURL(xhr.response);
        };
        xhr.open("GET", this.imageUrl);
        xhr.responseType = "blob";
        xhr.send();
        this.currentType = "url";
      }
    },
    youDao() {
      this.loading = true;
      let img = this.imgSrc.split(",")[1];
      let url = "/ocrapi";
      let curtime = this.$moment().format("X");
      let input =
        img.length > 20
          ? `${img.substr(0, 10)}${img.length}${img.substr(-10)}`
          : img;
      let appKey = "0e4b684d8bbb0541";
      let key = "Abqjj1QE6Xk2QxpEzD2a9z2hr8DO56sW";
      let salt = `11${curtime}`;
      // console.log(input);
      let data = {
        img: img,
        langType: "auto",
        detectType: "10012",
        imageType: "1",
        appKey: appKey,
        salt: salt,
        sign: sha256(`${appKey}${input}${salt}${curtime}${key}`),
        docType: "json",
        signType: "v3",
        curtime: curtime
      };
      // console.log(data);
      this.$axios
        .post(url, qs.stringify(data))
        .then(response => {
          // console.log(response);
          this.responseMessage = response;
          this.loading = false;
        })
        .catch(error => {
          // console.log(error);
          this.errorMessage = error;
          this.loading = false;
        });
    },
    imageToText() {
      this.loading = true;
      let type = this.currentType == "url" ? "url" : "base64Image";
      let formData = new FormData();
      formData.append("apikey", "c846d2a32888957");
      formData.append("isOverlayRequired", true);
      formData.append("language", this.model1);
      formData.append(type, this.imgSrc);
      //   console.log(formData);
      this.$axios
        .post(`https://api.ocr.space/parse/image`, formData)
        .then(response => {
          //   console.log(response);
          this.loading = false;
          this.value6 = response.data.IsErroredOnProcessing
            ? response.data.ErrorMessage[0]
            : response.data.ParsedResults[0].ParsedText;
        })
        .catch(error => {
          this.errorMessage = error;
          // console.log(error);
        });
    }
  }
};
</script>

<style lang="scss">
.orc {
  width: 1440px;
  height: 1024px;
  background: #f4f6f8;
  padding: 40px;
  display: flex;
}
.orc-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  width: 50%;
}
.orc-left_part1 {
  width: 650px;
  height: 472px;
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  position: relative;
}
.orc-left_part1-action {
  border-radius: 8px;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0px;
  left: 0px;
  background: #9e9e9e8a;
  transition: all 1s;
}
.orc-left_part1:hover .orc-left_part1-action {
  opacity: 1;
  visibility: visible;
}
.upload-image,
.online-url {
  width: 100px;
  height: 40px;
  background: #ffffff;
  border: 1px solid #d3d3d5;
  border-radius: 20px;
  outline: none;
  font-size: 18px;
  line-height: 40px;
  color: #d3d3d5;
  cursor: pointer;
  margin-left: 10px;
  margin-right: 10px;
}
.orc-left_part1-img {
  //   width: 650px;
  max-width: 100%;
  max-height: 100%;
}
.part1-input {
  width: 100%;
}
.orc-left_part2 {
  width: 650px;
  height: 331px;
  text-align: left;
  //   background: #c4c4c4;
}
.orc-left_part2-row {
  margin-bottom: 20px;
}
.orc-left_part3 {
}
.image-to-text {
  width: 200px;
  height: 60px;
  background: rgba(70, 94, 239, 0.9);
  box-shadow: 0px 10px 20px rgba(70, 94, 239, 0.68);
  border-radius: 30px;
  font-size: 24px;
  color: #ffffff;
}
// .image-to-text:focus {
//   border: 1px;
//   border-radius: 30px;
// }
.orc-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  height: 100%;
  width: 50%;
}
.orc-right_part1,
.orc-right_part2,
.orc-right_part3 {
  width: 650px;
  height: 280px;
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
textarea.ivu-input {
  border: none;
}
.orc-left_part3 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.translated-text {
  width: 200px;
  height: 60px;
  background: rgba(70, 94, 239, 0.9);
  //   box-shadow: 0px 10px 20px rgba(70, 94, 239, 0.68);
  border-radius: 30px;
  font-size: 24px;
  //   line-height: 27px;
  color: #ffffff;
  margin-left: 10px;
}
</style>
